<template>
  <div>
    <div class="searchs">
      <el-form :inline="true" @submit.native.prevent="onSubmit" :model="searchs" class="form-search">
        <el-row :gutter="24">
          <el-col>
            <el-form-item label="账号">
              <el-input v-model="searchs.keyword_search_value" placeholder="姓名/手机号"></el-input>
            </el-form-item>
            <el-form-item label="项目名称">
              <el-input v-model="searchs.name" placeholder="项目查询"></el-input>
            </el-form-item>
            <el-form-item label="金额">
              <el-input v-model="searchs.begin_money"></el-input>
            </el-form-item>
            <el-form-item label="至">
              <el-input v-model="searchs.end_money"></el-input>
            </el-form-item>
            <el-form-item label="投入时间">
              <el-date-picker
                v-model="addtime"
                type="daterange"
                align="right"
                unlink-panels
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
              />
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="类型">
              <el-select v-model="searchs.type" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.type" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="状态">
              <el-select v-model="searchs.status" clearable placeholder="请选择">
                <el-option v-for="(value, name) in config.status" :key="name" :label="value" :value="name"> </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" native-type="submit">查询</el-button>
            </el-form-item>
          </el-col>
          <el-col>
            当前已收益：<span style="color: red;">{{ data.sum_ok_money }}元</span> 当前未收益：<span style="color: red;"
              >{{ data.sum_on_money }}元</span
            >
          </el-col>
        </el-row>
      </el-form>
    </div>
    <el-table :data="tableData" class="list" v-loading="loading">
      <el-table-column prop="id" label="订单号" align="center" />
      <el-table-column prop="item_name" label="项目名" align="center" />
      <el-table-column prop="name" label="姓名" align="center" />
      <el-table-column prop="mobile" label="手机号" align="center" width="110" />
      <el-table-column prop="money" label="投入金额" align="center" />
      <el-table-column prop="days" label="天数" align="center" />
      <el-table-column prop="apr" label="利率" align="center" />
      <el-table-column prop="level_apr" label="加息率" align="center" />
      <el-table-column prop="pack" label="红包" align="center" />
      <el-table-column prop="top_apr" label="上级分润" align="center" />
      <el-table-column prop="back_money" label="返还金额" align="center" width="100" />
      <el-table-column prop="status_name" label="状态" align="center" />
      <el-table-column prop="type_name" label="类型" align="center" />
      <el-table-column prop="next_apr_no" label="当前期数" align="center" />
      <el-table-column prop="next_apr_date" label="下次收益时间" align="center" width="170" />
      <el-table-column prop="addtime_date" label="投入时间" align="center" width="170" />
      <el-table-column prop="address" align="center" label="操作" width="270">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row.id)" type="primary" size="small">查看详情</el-button>
          <el-button @click="clearList(scope.row.id)" type="warning" size="small" v-if="scope.row.status === 'D'"
            >清除订单
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="total, prev, pager, next"
      :total="count"
      :current-page="page_curren"
      :hide-on-single-page="false"
      @current-change="currentChange"
      class="pagination"
    />
  </div>
</template>
<script>
import dayjs from 'dayjs'
import Fetch from '../../utils/fetch'
import { Message } from 'element-ui'

export default {
  data() {
    return {
      dialogFormVisible: false,
      searchs: {
        keyword_search_value: '',
        begin_addtime: '',
        end_addtime: '',
        begin_money: '',
        end_money: '',
        status: '',
        type: '',
      },
      config: {
        type: null,
        status: null,
      },
      loading: false,
      all_money: 0,
      tableData: [],
      count: 0,
      page_curren: 1,
      addtime: [],
      data: {},
      pickerOptions: {
        shortcuts: [
          {
            text: '本月',
            onClick(picker) {
              picker.$emit('pick', [new Date(), new Date()])
            },
          },
          {
            text: '今年至今',
            onClick(picker) {
              const end = new Date()
              const start = new Date(new Date().getFullYear(), 0)
              picker.$emit('pick', [start, end])
            },
          },
          {
            text: '最近六个月',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              start.setMonth(start.getMonth() - 6)
              picker.$emit('pick', [start, end])
            },
          },
        ],
      },
    }
  },
  watch: {
    addtime(val) {
      if (val) {
        this.searchs.begin_addtime = dayjs(val[0]).format('YYYY-MM-DD')
        this.searchs.end_addtime = dayjs(val[1]).format('YYYY-MM-DD')
      } else {
        this.searchs.begin_addtime = ''
        this.searchs.end_addtime = ''
      }
    },
  },
  mounted() {
    this.FetchList()
  },
  methods: {
    FetchList() {
      this.loading = true
      Fetch('/item/dqlist/getlist', {
        page_curren: this.page_curren,
        ...this.searchs,
      })
        .then(r => {
          this.loading = false
          this.all_money = r.data.all_money
          this.count = r.data.count
          this.page_curren = Math.floor(r.data.page_curren)
          this.tableData = r.data.list
          this.config = r.data.config
          this.data = r.data
        })
        .catch(() => (this.loading = false))
    },
    currentChange(page) {
      this.page_curren = page
      this.FetchList()
    },
    onSubmit() {
      this.page_curren = 1
      this.FetchList()
    },
    handleClick(id) {
      this.$router.replace('/itemdq/apr/' + id)
    },
    clearList(id) {
      this.$confirm('此操作将永久清除该订单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.loading = true
          Fetch('/item/dqlist/reset', {
            id: id,
          })
            .then(() => {
              Message.success('清除成功')
              this.loading = false
              this.FetchList()
            })
            .catch(() => (this.loading = false))
        })
        .catch(() => {})
    },
  },
}
</script>
